.rectangleContainer{
	background: #343a40;
	height: 160px;
	align-items: center;
	padding: 48px 48px;
}

.heading{
	color: #fff;
	font-weight: normal;
	font-size: 32px;
	margin-bottom: 16px;
}

.desc{
	color: #fff;
	font-weight: normal;
	font-size: 16px;
}

.container{
	z-index: 1;
	width: 100%;
	max-width: 1300px;
	margin-right: auto;
	margin-left: auto;
	padding-right: 50px;
	padding-left: 50px;
}

.container > .heading{
	margin-top: 30px;
	color: #343a40;
	font-size: 24px;
	font-weight: bold;
	border-bottom: 1px solid #ccc;
}

.container > .syntax{
	margin-top: 30px;
	color: #343a40;
	font-family: "Lucida Console", Courier, monospace; 
 	font-size: 16px;
	font-weight: normal;
}